.component-item{
    display: flex;
    float: left;
    width: 60px;
    height: 30px;
    padding: 2px ;
    justify-content: space-around;
    border-radius: 2px ;
    margin: 20px 40px 0 40px;
    color: blue;
    cursor: pointer;
    box-shadow: 3px 3px 10px #bababa, -5px -5px 10px #ffffff;
    border-radius: 2vw;
    &:hover{
        transition:  all .1s;
        transform: scale(1.1);
        color: blue;
    }
}